
import Vue from 'vue'
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import Toast from 'vux/src/components/toast/index.vue'

import Drive from '@/tool/classFactory/drive.js';
import {getCookie} from '@/tool/common.js';
import VueClipboard from 'vue-clipboard2'
Vue.use(VueClipboard)

export default {
components: {
Group,
Cell,
Toast
},
data () {
return {
avartar: 'https://wx.qlogo.cn/mmopen/vi_32/DYAIOgq83eoLKnzzlSdMqQzIPhdxIZ2JyROWeajaMMib8Lq8PfmDnic3ou9oU7jJ8U21Esu4OQibRr04ccYy8THXA/132',
charter:[
{
name:'完善信息',
url:'../../images/icons/c_icon_01@2x.png'
}
],
params:{
'sessionId':''
},
params02:{
'sessionId':'',
'isUse':'1'
},
params03:{
'path':'/teacher'
},
coachMsg:{

},
isOpenId:null,
cardList:[],
userStatus: 1,
showPositionValue: false,
msg: '复制成功',
middle:'middle'
}
},
created () {
this.params02.sessionId = this.params.sessionId = getCookie('sessionId')
console.log(this.params.sessionId)
this.getCoachMsg()
this.cardResultList()
},
methods: {
getCoachMsg(){
const that = this;
Drive.prototype.getCoachData(that.params).then(res => {
let list = res.data.data || [];
that.coachMsg = list
console.log(that.coachMsg)
});
},
cardResultList(){
const that = this;
Drive.prototype.cardResultList(that.params02).then(res => {
let list = res.data.data || [];
console.log('hcj')
console.log(list)
that.isOpenId = list.isOpenId
that.cardList = []
that.cardList = list.cardResultList
console.log(that.coachMsg)
});
},
onTypeTap(e){
this.userStatus = e
this.params02.isUse = e
this.cardResultList()
},
onCompleteMsgTap(){
const that = this
that.$router.push('../coachmsg')
},
onChangePwdTap(){
const that = this
that.$router.push('../changepwd')
},
onLogoutTap(){
const that = this;
Drive.prototype.exit(that.params).then(res => {
let list = res.data.data || {};
that.$router.push('../tealogin')
});
},
onCopyTap(index){

},
register(){
var url = 'http://jindou360.com:8080/jkrj/api/wechat/auth/register.jhtml?path=' + '/teacher'
window.location.href = url
},
onBindWechatTap(){
if(this.isOpenId != 1){
this.register()
}else {
this.showPositionValue = true
this.msg = '已绑定微信'
}

},
onCopy (e) {
console.log(e)
this.showPositionValue = true
},
onError (){

},
onTeaMsgTap (){
const that = this
that.$router.push('../coachmsg')
}
}
}



